import React, { useState, useEffect } from "react";
import axios from "axios";
import { QuillDeltaToHtmlConverter } from "quill-delta-to-html";
import PropTypes from "prop-types";
import { Tags } from "./Tags";
export const AppFullSizeTile = props => {
  var tile = props.tile ? props.tile : {};
  const [html, setHtml] = useState("");
  const [error, setError] = useState(null);
  useEffect(() => {
    if (tile.contentRef) {
      axios
        .get(tile.contentRef)
        .then(result => {
          var converter = new QuillDeltaToHtmlConverter(result.data.ops);
          setHtml(converter.convert());
        })
        .catch(error => {
          if (error.response.status !== 404) {
            setError(error.message);
          }
        });
    }
  });
  return (<>
    {error && <div>{error}</div>}
    <div onClick={props.onClose} style={{
      backgroundColor: tile.color,
      color: tile.textcolor,
      height: "80px",
      borderRadius:"40px",
      
      overflow: "auto"
    }}>
      <div>
        <div style={{ display: "flex" }}>
          <div >
            {" "}
            <img style={{ margin: "10px", marginLeft:"20px", height: "60px", width: "auto" }} src={tile.icon} />
          </div>
          <div style={{ 
          width: "auto" }}>
            <div className="fullSizeHeading" style={{
              padding: "10px",
              marginTop:"20px",
              fontSize: "48px",
              textOverflow: "nowrap",
              whiteSpace: "no-wrap"
            }}>
              {tile.title}
            </div>
            <div className="fullSizeDescription" style={{ paddingLeft: "3vh", fontSize: "3vh" }}>
          
              {tile.inShort}
            </div>
          </div>
        </div>


        {/* <div style={{ display: "flex", justifyContent: "flex-end" }}>
          {tile.jumpto && (<a style={{ height: 24 }} className="button" href={tile.jumpto} target="_blank">
           Launch
              </a>)}
          {tile.externalArticle && (<a style={{ height: 24 }} className="button" href={tile.externalArticle} target="_blank">
            Details
              </a>)}
        </div> */}
      </div>

    </div>
    <div style={{
          margin : "20px",
          xheight: "80vh",
         
          overflow: "auto",
          backgroundColor: "#ffffff",
          color: "#000000",
          padding: "20px"
        }}>
          <div style={{fontSize:"22px",lineHeight:"24px"}}>
            {tile.messageBeforeLaunch}
<div style={{marginTop:"20px"}}>
            {/* <textarea style={{width:"100%",height:"30px"}}>{tile.jumpto}</textarea> */}
            <div>

<a href={tile.jumpto}>{tile.jumpto}</a>

            </div>
            </div>
            </div>
            <div>

    
            </div>
          <Tags references={tile.references} />
          <div style={{ display: "flex", justifyContent: "flex-end" }}>
        {/* {tile.jumpto && (<a style={{ height: 24 }} className="button" href={tile.jumpto} target="_blank">
        Launch
            </a>)} */}
        {tile.externalArticle && (<a style={{ height: 24 }} className="button" href={tile.externalArticle} target="_blank">
          Details
            </a>)}
      </div>
          <div dangerouslySetInnerHTML={{ __html: html }}></div>
        </div>
  </>);
};
AppFullSizeTile.propTypes = {
  tile: Object,
  onClick: PropTypes.func
};
